.app-wrapper {
  
  width: 100vw;
  height: 100vh;
  background-color: #EDEEF0;
  display: grid;
  grid-template-areas: 
  "h h"
  "n c";

  grid-template-rows: 1fr 10fr;
  grid-template-columns: 2fr 10fr;
}

.app-wrapper-content {
  grid-area: c;
  margin: 20px;
  padding: 20px;
  border-radius: 3%;
  background-color: rgb(255, 255, 255);

}

